/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {

    .counter__img-contain {
        grid-column: 1/4;
        justify-self: center;
        align-self: start;
        
    }

    .counter__img-contain img {
        width: 100px;
    }

    .counter__border-contain {
        /* grid-column: 1/4; */
        /* align-self: center;
        justify-self: center; */
        /* display: flex; */
        position: relative;
        grid-column: 2/3;
        
        
    }
    
    .img__border {
        position: absolute;
        width: 80px;

    }

    .img__border2 {
        position: absolute; 
        
        
    }

    .img__border3 {
        position: absolute;
        width: 20px;
        

    }
    
  }

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {

    .counter__border-contain {
        position: relative;
    }

    
    .counter__img-contain {
        grid-column: 1/4;
        justify-self: center;
        align-self: start;
        
    }

    .counter__img-contain img {
        width: 400px;
    }

    .counter__img-logo img {
        margin-top: 30%;
    }

